/* Style for the main container */
.service-container {
  padding: 20px;
  text-align: center;
}

/* Flexbox grid to make the cards display side by side */
.services-grid {
  display: flex;
  flex-wrap: wrap; /* Allows wrapping to the next line if necessary */
  justify-content: center; /* Center the service items */
  gap: 20px; /* Spacing between the cards */
}

/* Styling each service card */
.service-card {
  flex-basis: calc(25% - 20px); /* Each card will take 25% of the container width minus the gap */
  box-sizing: border-box;
  padding: 10px;
  text-align: center;
}

/* Style for the service image */
.service-image {
  width: 100%; /* Make the image scale to the width of the card */
  height: auto; /* Maintain the image's aspect ratio */
  margin-top: 10px; /* Add some space between the title and the image */
  border-radius: 10px; /* Optional: round the image corners */
}

/* Style for the service name */
.service-card h2 {
  font-size: 18px;
  margin-bottom: 10px;
}
