/* 通用样式 */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    background-color: #f4f4f4;
    color: #333;
    margin: 0;
    padding: 0;
}

header {
    /* background-color: #333; */
    background-color: #035a2a;
    color: #fff;
    padding: 10px 0;
}

nav ul {
    list-style: none;
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
    position: relative;
}

nav ul li {
    position: relative;
    margin: 0 20px;
}

nav ul li:last-child {
    margin-left: auto;
}

nav ul li a {
    text-decoration: none;
    color: #fff;
    padding: 10px 20px;
    font-size: 18px;
    border-radius: 4px;
    transition: background-color 0.3s, color 0.3s;
}

nav ul li a:hover {
    background-color: #575757;
    color: #ffcc00;
}

/* 隐藏二级菜单，默认不显示 */
nav ul .dropdown {
    display: none;
    position: absolute;
    top: 100%;
    /* left: auto; */
    right: 0;
    background-color: #333;
    padding: 10px 0;
    list-style: none;
    min-width: 150px;
    z-index: 1;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

nav ul .dropdown li {
    margin: 0;
}

nav ul .dropdown li a {
    padding: 10px 20px;
    white-space: nowrap;
    display: block;
    text-align: left;
    color: #fff;
    transition: background-color 0.3s, color 0.3s;
}

nav ul .dropdown li a:hover {
    background-color: #575757;
    color: #ffcc00;
}

/* 点击或悬停时显示二级菜单 */
nav ul li:hover .dropdown, 
nav ul li a.has-dropdown:focus + .dropdown {
    display: block;
}

/* Toggle for focus behavior */
nav ul li a.has-dropdown:focus:after {
    content: ' ▲';
}

/* 为有二级菜单的一级菜单添加下箭头 */
nav ul li a.has-dropdown:after {
    content: ' ▼';
    font-size: 12px;
    color: #fff;
    position: relative;
    top: -3px;
}

/* 响应式设计 */
@media (max-width: 768px) {
    nav ul {
        flex-direction: column;
        align-items: flex-start;
    }

    nav ul li {
        margin: 10px 0;
    }
    nav ul li:last-child {
        margin-left: 0;
    }

    nav ul .dropdown {
        position: static;
        min-width: 100%;
        box-shadow: none;
    }
}

/* 轮播图的基本样式 */
.carousel {
    width: 80%;
    max-width: 1000px;
    height: 30%;
    margin: 20px auto;
    padding: 20px;
    overflow: hidden;
    position: relative;
}

.slideshow-container {
    position: relative;
    width: 100%;
    height: 400px;
    overflow: hidden;
}

.slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 1s ease;
}

.slide.active {
    opacity: 1;
}

.slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.map {
    text-align: center;
    max-width: 600px;
    height: 300px;
    margin: 20px auto;
}

main {
    max-width: 1000px;
    margin: 20px auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1, h2 {
    text-align: center;
    color: #333;
}

.empty-area {
    width: 100%;
    height: 200px;
}

footer {
    text-align: center;
    padding: 10px 0;
    background-color: #333;
    color: #fff;
    position: fixed;
    bottom: 0;
    width: 100%;
}


/* Styles for the popup (modal) */
.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
  }
  
  .modal-content {
    background-color: white;
    padding: 20px;
    max-width: 600px;
    width: 100%;
    position: relative;
    border-radius: 8px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  }
  
  .close-button {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .popup-button {
    display: block;
    margin: 20px auto;
    padding: 10px 20px;
    font-size: 16px;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .popup-button:hover {
    background-color: #575757;
  }
