.HomeTitleText {
    color: aliceblue;
    height: 90vh;
    z-index: 1;
    display: flex;
    text-align: center;
    justify-content: center; /* Vertically centers the content */
    align-items: center;
    flex-direction: column; /* Aligns children in a column */
}
.HomeTitleText h1 {
    color: aliceblue;
    margin: -20vh 0 0 0; /* 向上偏移20px，调整这个值来满足你的需求 */
    font-size: xx-large;
    
}
.HomeTitleText p {
    font-size: x-large;
}


