.employees-container {
    padding: 20px;
    text-align: center;
  }
  
  .employees-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }
  
  .employee-card {
    border: 1px solid #ccc;
    border-radius: 10px;
    overflow: hidden;
    width: 200px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .employee-photo {
    width: 100%;
    height: auto;
  }
  
  .employee-card h2 {
    margin: 10px 0;
    font-size: 18px;
  }
  